@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@400;600;800&display=swap);
body,
h1 {
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Raleway', sans-serif;
}

em {
  background: cyan;
  font-style: normal;
}
.left-panel { float: left; width: 250px; }

img {
  display: block;
  width: 180px;
  border-radius: 10px;
  height: auto;
  box-shadow: 0 2px 4px 0 rgb(0 0 0 / 10%);
}

.header {
  display: flex;
  align-items: center;
  min-height: 45px;
  font-size: 24px;
  font-weight: 600;
  margin-bottom: 1.5rem;
}

.container {
  max-width: 1300px;
  margin: 0 auto;
  padding: 1rem;
  display: flex;
  flex-direction: column;
}

a {
  color: #0f0f0f;
  text-decoration: none;
}

.metas-container {
  display: flex;
  margin-bottom: 2rem;
  justify-content: space-between;
  align-items: center;
}

.clear-refinements {
  width: 250px;
}

.books-container {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  grid-gap: 30px 15px;
  gap: 30px 15px;
  margin-top: 2rem;
}

.show-more {
  width: 250px;
  margin: 2rem auto;
}

.sliders {
  margin-top: 2rem;
}

.section-header {
  margin-top: 2.5rem;
}

.product-card-container {
  display: flex;
  flex-direction: column;
  width: 180px;
}

.product-card-details {
  display: flex;
  flex-direction: column;
  padding: 1.5rem 0 1rem;
  width: 100%;
}

.product-card-name {
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 12px;
}

.product-card-author {
  font-size: 14px;
}

.search-box {
  margin-bottom: 2rem;
}

.search-panel {
  width: calc(98% - 250px);;
}

.pagination {
  margin: 2rem auto;
  text-align: center;
}

.results {
  display: flex;
  grid-gap: 30px;
  gap: 30px;
}

.loader {
  margin: 12rem auto ;
}

img.details {
  width: 270px;
  border-radius: 20px;
}

.back-btn {
  background-color: transparent;
  border: none;
  font-size: 16px;
  height: auto;
  width: 100px;
}

.back-btn::before {
  content: '\140A';
  padding: 4px
}

.action-btn {
  width: 250px;
}

.ais-ClearRefinements-button {
  padding: 20px;
}

.actions {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 110px;
}

.product-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.product-details {
  display: flex;
  flex-direction: column;
  width: 45%;
  height: auto;
}

.product-title {
  font-size: 22px;
  font-weight: 600;
  margin-bottom: 12px;
  margin-right: 10px;
}

.product-name {
  display: flex;
  align-items: baseline;
}

.product-author {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 12px;
}

.product-subtitle {
  font-size: 22px;
  margin-left: 10px;
  margin-bottom: 12px;
}

.product-metas {
  font-size: 16px;
  margin-bottom: 12px;
  margin-top: 20px;
  display: flex;
  flex-direction: column;
}
.product-pages {
  margin-bottom: 10px;
}

.about-section {
  margin-top: 20px;
}

.about {
  font-weight: 600;
  margin-bottom: 10px;
}

.product-description {
  line-height: 25px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 6;
  line-clamp: 6;
  -webkit-box-orient: vertical
}

.container.product-page {
  max-width: 1100px;
}

.header-container {
  max-width: 1300px;
  margin: 0 auto;
  color: #9698c3;
  height: 70px;
  display: flex;
  align-items: center;
}

.header-bkgd {
  box-shadow: 0 2px 4px 0 rgb(0 0 0 / 10%);
  background: white;
  margin-bottom: 1rem;
}

.logo {
  font-size: 34px;
  font-weight: 800;
}

.logo::before {
  content: '\2712';
  font-size: 38px;
  padding: 0.5rem;
}

